//---------------NotyfNotyfNotyfNotyf----------//////
import { Notyf } from 'notyf';
import 'notyf/notyf.min.css'; // for React and Vue

// Create an instance of Notyf
const notyf = new Notyf({
  duration: 5000,
  position: {
    x: 'center',
    y: 'bottom',
  },
  types: [
    {
      type: 'success',
      backgroundColor: 'orange',
      icon: false,
    },
    {
      type: 'error',
      backgroundColor: 'indianred',
      duration: 2000,
      dismissible: true,
    },
    {
      type: 'info',
      backgroundColor: 'white',
      color: 'black',
      icon: false,
    },
  ],
});

// Display an error notification
const btnOk = document.querySelector('.btn-ok');
const btnErr = document.querySelector('.btn-error');
const btnInfo = document.querySelector('.btn-info');

btnOk.addEventListener('click', notyfOk);
btnErr.addEventListener('click', notyfErr);
btnInfo.addEventListener('click', notyfInfo);

function notyfOk() {
  notyf.success('success_success_success');
}
function notyfErr() {
  notyf.error('ERROR');
}

function notyfInfo() {
  notyf.open({
    type: 'info',
    message: 'Send us <b>an email</b> to get support',
  });
}

////////////////////////------npm i siema------------------////////////////////////
import Siema from 'siema';
import gallery from './gallery-items.js';
const listImages = document.querySelector('.siema');
renderImages(gallery);

const mySiema = new Siema({ loop: true });
document.querySelector('.prev').addEventListener('click', () => mySiema.prev());
document.querySelector('.next').addEventListener('click', () => mySiema.next());

function renderImages(array) {
  array.forEach(e => {
    const itemImage = `
    <div><img
    class="gallery__image"
    src="${e.preview}"
    data-source="${e.original}"
    alt="${e.description}"
    /></div>`;

    listImages.insertAdjacentHTML('beforeend', itemImage);
  });
}
